import axios from "axios";

export const toyService = {
  query,
  deleteToy,
  save,
  getById,
  getEmptyToy,
};
function _getUrl(id=''){
  const BASE_URL=(process.env.NODE_ENV!=='development')? '/api/toy':'//localhost:3000/api/toy'
  return `${BASE_URL}/${id}`
}
function query(filterBy) {
  //!! fix the filter
  let filterQuery = `name_like=${filterBy.name}&type_like=${filterBy.type}`;
  filterQuery += filterBy.inStock === "all" ? "" : `&inStock=${filterBy.inStock}`;
  filterQuery+=`&_sort=${filterBy.sortBy}&_order=asc`
  return axios
    .get(_getUrl())
    .then((res) => res.data);
}
function getById(toyId) {
  return axios
    .get(_getUrl(toyId))
    .then((res) => res.data);
}

function deleteToy(toyId) {
  return axios
    .delete(_getUrl(toyId))
    .then((res) => res.data);
}
function _add(toy) {
  toy.createdAt = Date.now();
  return axios.post(_getUrl(), toy).then((res) => res.data);
}

function _update(toy) {
  return axios
    .put(_getUrl(toy._id), toy)
    .then((res) => res.data);
}

function save(toy) {
  return toy._id ? _update(toy) : _add(toy);
}

function getEmptyToy(){
  return {
    name: "",
    price: 0,
    type: "",
    inStock: false,
  }
   
}
