<template>
  <div class="toy-list flex wrap">
    <toy-preview v-for="toy in toys" :toy="toy" :key="toy._id" @delete="onDelete" />
  </div>
</template>

<script>
import toyPreview from "../components/toy-preview.vue"
export default {
  name: "toy-list",
  props: ["toys"],
  methods: {
    onDelete(toyId) {
      this.$emit("delete", toyId);
    },
  },
  components: {
    toyPreview,
  },
};
</script>

