<template>
  <div class="toy-details">
    <h1 class="cmp-title">Toy Details:</h1>
    <button class="back-btn" @click="goBack">Go Back</button>
    <section class="details" v-if="toy">
      <h2>Toy Id: {{ toy._id }}</h2>
      <h2>Name: {{ toy.name }}</h2>
      <h3>Price: ${{ toy.price }}</h3>
      <h3>Toy Type: {{ toy.type }}</h3>
      <h3>Created At: {{ toy.createdAt }}</h3>
      <h3>In Stock?: {{ toy.inStock }}</h3>
    </section>
    <h3 v-else>Loading Toy...</h3>
  </div>
</template>

<script>
import {toyService} from '../services/toy-service.js'

export default {
  name: "toy-details",
  data() {
    return {
      toy: null,
    };
  },
  created() {
    let id = this.$route.params.id;
    toyService.getById(id)
      .then((toy) => (this.toy = toy));
  },
  methods: {
    goBack() {
      this.$router.push("/");
    },
  },
};
</script>

<style src="../assets/css/toy-details.css"></style>
