<template>
  <div class="toy-edit" v-if="toy">
    <h1 class="cmp-title">{{ getTitle }}</h1>
    <button @click="goHome" class="back-btn">Go Back</button>
    <form @submit.prevent="saveToy" class="details col-layout">
      <label for="toyName">Toy Name:</label>
      <input
        type="text"
        v-model="toy.name"
        placeholder="Enter Toy Name"
        id="toyName"
      />
      <label for="toyPrice">Price:</label>
      <input type="number" v-model.number="toy.price" min="0" id="toyPrice" />
      <label for="toyType">Type:</label>
      <select id="toyType" v-model="toy.type">
        <option value="adult">Adult</option>
        <option value="funny">Funny</option>
        <option value="educational">Educational</option>
      </select>
      <label for="inStock">In Stock?</label>
      <select id="inStock" v-model="toy.inStock">
        <option value="true">Yes</option>
        <option value="false">No</option>
      </select>
      <button @click="saveToy">Save</button>
      <img src="" alt="" />
    </form>
  </div>
</template>

<script>
import {toyService} from '../services/toy-service.js'

export default {
  name: "toy-edit",
  data() {
    return {
      toy: null,
    };
  },
  created() {
    let id = this.$route.params.id;
    if (id) {
    toyService.getById(id)
      .then((toy) => (this.toy = toy));
    }else this.toy= toyService.getEmptyToy()
  },
  methods: {
    goHome() {
      this.$router.push("/");
    },
    saveToy() {
      let toy=JSON.parse(JSON.stringify(this.toy))
      this.$store.dispatch({type:'saveToy',toy})
        }
    },
  computed: {
    getTitle() {
      if (!this.toy._id) return "Add New Toy:";
      return "Edit Toy Details:";
    },
  },
};
</script>

<style src="../assets/css/toy-edit.css" scoped></style>
