<template>
  <div class="toy-app">
    <toy-filter @filter="setFilter" />
    <toy-list :toys="toys" @delete="deleteToy" />
  </div>
</template>

<script>
import toyList from "../components/toy-list.vue";
import toyFilter from "../components/toy-filter.vue";
export default {
  name: "toy-app",
  data() {
    return {
      filterBy: {
        name: "",
        inStock: "all",
        type: "",
        sortBy:'name',
      },
    };
  },
  created() {
    this.$store
      .dispatch({ type: "loadToys"})
  },
  computed:{
    toys(){
      return this.$store.getters.toys
    }
  },
  methods: {
    deleteToy(toyId) {
      this.$store.dispatch({ type: "deleteToy", id: toyId });
    },
    setFilter(filterBy) {
      this.$store.commit({type:'setFilterBy',filterBy:{...filterBy}})
    },
    
  },
  components: {
    toyList,
    toyFilter,
  },
};
</script>

<style src="../assets/css/toy-app.css" scoped></style>
