<template>
  <section class="toy-preview col-layout">
    <h3>Name: {{ toy.name }}</h3>
    <h5>Price: ${{ toy.price }}</h5>
    <h5>Toy Type: {{ toy.type }}</h5>
    <h5>In Stock?: {{ toy.inStock }}</h5>
    <section class="actions flex space-between">
      <button @click="onDelete">Delete</button>
      <button @click="onEdit">Edit</button>
      <button @click="showDetails">Details</button>
    </section>
  </section>
</template>

<script>
export default {
  name: "toy-preview",
  props: ["toy"],
  methods: {
    onDelete() {
      this.$emit("delete", this.toy._id);
    },
    onEdit() {
      this.$router.push(`/edit/${this.toy._id}`);
    },
    showDetails() {
      this.$router.push(`/toy/${this.toy._id}`);
    },
  },
};
</script>

<style src="@/assets/css/toy-preview.cmp.css" scoped></style>
